.mostbet {
  min-height: 100vh;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mostbet__logo {
  pointer-events: none;
  position: absolute;
  left: 50%;
  top: 8%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 15vw;
  height: 15vw;
  object-fit: contain;
}

.mostbet__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: brightness(50%);
  background-image: url(../images/win1/win1.jpg);
  background-repeat: no-repeat;
  background-position: left center;
  background-attachment: fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.mostbet-popup {
  border-radius: 4vw;
  border: 0.2vw solid rgba(117, 0, 0, 0.19);
  background: rgba(6, 0, 117, 0.28);
  backdrop-filter: blur(0.5vw);
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  height: 50vh;
  width: 50vw;
  z-index: 10;
  padding: 3vw 3vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mostbet-popup__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2vw;
  width: 100%;
}

.mostbet-popup__title {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 4vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  max-width: 40vw;
  margin: 0 auto;
  font-style: italic;
  text-transform: uppercase;
}

.mostbet-popup__title span {
  color: yellow;
}

.mostbet-popup__wrap {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

.mostbet-popup__link {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 2.5vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border-radius: 1vw;
  padding: 1vw 4vw;
  display: block;
  transition: all 0.4s;
  animation: pulse infinite 1.5s;
}

.mostbet-popup__link.color {
  background: #fb4d00;
  box-shadow: 12px 12px 12px rgba(251, 77, 0, 0.5);
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    -o-transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.mostbet-popup__link:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

